.section-sellers {
  color: #111;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

.section-sellers-text {
  letter-spacing: -.28px;
  line-height: 1.285;
}

.sellers-container {
  max-width: 335px;
  margin: 0 auto;
}

h2, ul, p {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.sellers-modal-title {
  text-align: center;
  letter-spacing: -.6px;
  text-transform: uppercase;
  margin-bottom: 32px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
}

.sellers-title-color {
  color: #fd9222;
}

.section-sellers-text.text-mobile {
  max-width: 306px;
  margin-top: 20px;
}

.sellers-list {
  flex-direction: column;
  display: flex;
}

.list-item:first-child {
  margin-bottom: 32px;
}

.sellers-img {
  width: 100%;
  height: auto;
  border-radius: 15px;
  display: block;
}

.section-sellers-text.text-tablet {
  display: none;
}

.button {
  color: #fff;
  cursor: pointer;
  background: #fd9222;
  border: 0;
  border-radius: 100px;
  padding: 12px 28px;
  font-weight: 600;
  line-height: normal;
}

.buy-now-btn {
  margin-top: 20px;
}

.sellers-text-btn {
  justify-content: center;
  display: flex;
}

.button:hover, .button:focus {
  color: #fd9222;
  background: #fcf1e0;
  transition: all .3s cubic-bezier(.43, .65, .83, .67);
  transform: scale(1.05);
}

.list-item:last-child {
  display: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(2px);
  opacity: 1;
  z-index: 3;
  background: rgba(0, 0, 0, .2);
  flex-wrap: wrap;
  justify-content: center;
  transition: all .3s cubic-bezier(.43, .65, .83, .67);
  display: flex;
  position: fixed;
  top: 0;
  overflow-y: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  transition: all .3s cubic-bezier(.43, .65, .83, .67);
}

.modal-content {
  width: 311px;
  background: #fafafa;
  border-radius: 15px;
  margin: 30px auto;
  padding: 32px 24px;
  position: relative;
}

.sellers-close-btn {
  cursor: pointer;
  stroke: currentColor;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 14px;
  right: 14px;
}

.sellers-close-btn:hover {
  stroke: #fd9222;
}

.modal-list {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.modal-list-item {
  background: #fff;
  border: 1px solid rgba(17, 17, 17, .1);
  border-radius: 15px;
  justify-content: space-between;
  align-items: center;
  padding: 8px 16px;
  display: flex;
}

.prod-weight {
  color: #111;
  background: #fafafa;
  border: 1px solid rgba(17, 17, 17, .1);
  border-radius: 100px;
  padding: 6px 16px;
  font-weight: 600;
  line-height: normal;
}

.label-text {
  color: #000;
  letter-spacing: -.24px;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.166;
}

.modal-input {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border: 1.5px solid rgba(17, 17, 17, .05);
  border-radius: 8px;
  outline: 0;
  padding: 14px 18px;
  box-shadow: 0 5.44492px 5.44492px rgba(0, 0, 0, .03);
}

.modal-form .label:not(:first-child) {
  margin-top: 18px;
}

.personal-info {
  flex-direction: column;
  gap: 14px;
  margin-top: 8px;
  display: flex;
}

.personal-info-container {
  margin-top: 32px;
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.28px;
}

input::placeholder, textarea::placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.28px;
}

.modal-input:focus {
  border-color: #fd9222;
  transition: all .3s cubic-bezier(.43, .65, .83, .67);
}

.email {
  flex-wrap: wrap;
  display: flex;
}

.phone-num {
  flex-wrap: wrap;
  display: flex;
  position: relative;
}

.sellers-submit-btn {
  margin-top: 30px;
}

.phone-input {
  padding-left: 106px;
}

.icon-flag {
  position: absolute;
  top: 53%;
  left: 18px;
}

.icon-card {
  position: absolute;
  top: 53%;
  right: 18px;
}

.country-code {
  color: #000;
  letter-spacing: -.28px;
  font-weight: 500;
  line-height: normal;
  position: absolute;
  top: 53%;
  left: 50px;
}

.phone-num .country-code:after {
  content: "";
  width: 1px;
  height: 100%;
  background: rgba(0, 0, 0, .1);
  display: block;
  position: absolute;
  bottom: 0;
  left: 140%;
}

.credit-card {
  flex-wrap: wrap;
  display: flex;
  position: relative;
}

.comment-textarea {
  resize: none;
  padding: 14px 18px 60px;
}

.textarea {
  flex-wrap: wrap;
  display: flex;
}

@media screen and (min-width: 768px) {
  .sellers-container {
    max-width: 704px;
  }

  .sellers-img {
    width: auto;
  }

  .sellers-list {
    flex-direction: row;
    gap: 16px;
  }

  .section-sellers-text.text-mobile {
    display: none;
  }

  .section-sellers-text.text-tablet {
    width: 306px;
    display: block;
  }

  .sellers-text-btn {
    justify-content: space-between;
    align-items: center;
  }

  .buy-now-btn {
    align-self: flex-start;
    margin: 0;
    padding: 14px 40px;
    font-size: 18px;
  }

  .sellers-modal-title {
    letter-spacing: -.72px;
    margin-bottom: 50px;
    font-size: 24px;
    line-height: 1.166;
  }

  .modal-content {
    width: 609px;
    padding: 48px;
  }

  .sellers-close-btn {
    top: 24px;
    right: 24px;
  }

  .sellers-modal-title {
    margin-bottom: 32px;
  }

  .modal-list {
    flex-direction: row;
    gap: 16px;
  }

  .modal-list-item {
    flex-direction: column;
    padding: 24px 20px 18px;
  }

  .personal-info {
    flex-direction: row;
    gap: 21px;
  }

  .personal-info-container {
    margin-top: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .sellers-container {
    max-width: 1136px;
  }

  .list-item:last-child {
    display: block;
  }

  .section-sellers-text.text-tablet {
    width: 407px;
  }
}

